@import url(https://stackpath.bootstrapcdn.com/font-awesome/4.7.0/css/font-awesome.min.css);
@import url(https://fonts.googleapis.com/css2?family=Red+Hat+Text:wght@400;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.menu {  
  box-shadow: 0px 0px 20px 2px rgba(0, 0, 0, 0.2);
  display: flex;
  justify-content: space-between;
  align-items: center;  
  background-color:#fff;
  
}

.menu ul {
  list-style: none;  
  padding: 0;  
  display: flex;
  flex-wrap: wrap;
}

.menu ul li {
  border: 0px solid black;  
  padding: 20px;  
  transition: 0.7s;  
}

.menu ul li:hover {
  background-color: #dfe1eb;

}

.menu ul li i {
  margin-right: 10px;
}

.menu__link {
  text-decoration: none;
  color: #333;
  margin-right: 0px;
}

.topbar__dev {
  border: 0px solid green;
  font-size: 12px;
  margin-right: 50px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;   
}

.topbar__dev-title {
  font-size: 12px;
  color: #333;
}

.topbar__dev-socials {
  border: 0px solid black;
  margin: 5px 0;
}

.topbar__dev-socials a {
  margin: 10px;
  font-size: 20px;
  color: #333;
}
.topbar__logo {
  border: 0px solid black;   
  margin-left: 30px;  
}

.topbar__logo img {
  height: 50px;
}

.topbar {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.register * {
  font-family: 'Red Hat Text', sans-serif;  
}

.register {
  border: 0px solid #11A829;  
}

.register__title {    
  border: 0px solid black;
  text-align: center;
  padding: 30px;
  font-size: 18px;
}

.register__title i {
  margin-right: 10px;
}

.register__form {
  border: 0px solid black;
  display: flex;
  flex-direction: column;
  margin: 0 auto;
  width: 500px;
  padding: 30px;  
  box-shadow: 0px 0px 20px 2px rgba(0, 0, 0, 0.2);
}

.register__input {
  border: 0px solid gold;    
}

.register__input input {
  border: 1px solid #ccc;  
  padding: 10px;
  margin: 10px 0;
  width: 100%;
  
}

.register__button {
  padding: 15px;
  margin: 20px 0;
  width: 100%;
  cursor: pointer;
  background: #11A829;
  color: white;
  font-size: 18px;
  font-weight: bold;
}

.list * {
  font-family: 'Red Hat Text', sans-serif;  
}

.list {
  border: 0px solid #11A829;  
}

.list__title {    
  border: 0px solid black;
  text-align: center;
  padding: 30px;
  font-size: 18px;
}

.list__title i {
  margin-right: 10px;
}

.list__table {
  border-spacing: 0;
  border: 0px solid black;    
  margin: 0 auto;
  width: 90%;  
  box-shadow: 0px 0px 20px 2px rgba(0, 0, 0, 0.2);

}
.list__table thead {
  background-color:#49A0EF;
  border: 0;
  padding: 0;
  margin: 0;  
}

.list__table thead tr {
  padding: 0;
}

.list__table thead tr th {  
  padding: 20px;  
  color: white;
  text-align: left;  
}


.list__table tbody tr td {  
  padding: 20px;
  margin: 0;  
  color: #555;
  text-align: left;  
  background-color: #eee;
  font-size: 14px;
}

.list__table tbody tr td i {  
  margin: 10px;  
  font-size: 18px;
}

* { 
  margin: 0;
  border: 0;
  outline: 0;
  box-sizing: border-box;
}

body{
  font-family: 'Red Hat Text', sans-serif;  
}

.container {  
  margin: 50px 0;  
  border: 0px solid tomato;
}
